import { createContext, useEffect, useState } from "react";
import CompCurrent from "./comp/CompCurrent";
import CompDaily from "./comp/CompDaily";
import CompDetail from "./comp/CompDetail";
import CompHeader from "./comp/CompHeader";
import CompHourly from "./comp/CompHourly";
import CompMap from "./comp/CompMap";
import { fnGetAddress, fnGetCurrentLocation, fnGetWeather } from "./store/function";
export const AppContext = createContext()

const App = () => {
  let [stLatLng, stChangeLatLng] = useState(null) //위도와 경도 스테이트 변수
  let [stAddress, stChangeAddress] = useState(null) //주소 정보
  let [stWeather, stChangeWeather] = useState(null)//날씨정보
  //fnGetCurrentLocation() //위경도를 반환하는 함수
  //fnGetAddress(37.4905399, 126.7280586)
  //fnGetWeather(37.4905399, 126.7280586)

  const fnInit = async () => { //promise로 만든 비동기 함수를 사용하기 위해서 async await로 구현
    //let latLng = await fnGetCurrentLocation() //현재위치의 위경도를 객체(위도, 경도)로 반환
    //fnGetAddress(latLng.lat, latLng.lng)

    let { lat, lng } = await fnGetCurrentLocation() //구조분해 문법
    let address = await fnGetAddress(lat, lng)//위경도를 받아서 주소반환
    let weather = await fnGetWeather(lat, lng)//위경도를 받아서 날씨데이터 반환

    //날씨 정보에 따라 배경 변경하기
    let bg = weather.current.weather[0].main
    document.getElementById('root').style.backgroundImage = `url(${process.env.PUBLIC_URL}/img/weather/${bg}.jpg)`


    //화면을 바꾸지기 위해서 state변수 변경
    stChangeLatLng({ lat, lng })
    stChangeAddress(address)
    stChangeWeather(weather)


    console.log(lat, lng)
    console.log(address)
    console.log(weather)
  }//fnInit

  useEffect(() => {
    fnInit()
  }, [])//useEffect



  return (
    <AppContext.Provider value={{
      stLatLng, stChangeLatLng, stAddress, stChangeAddress, stWeather, stChangeWeather,
      fnInit
    }}> {/*AppContext.Provider를 사용해 날씨 정보를 다른 Comp파일에 모두 전달*/}
      <CompHeader />
      <main className="app-container scr-style">
        <CompCurrent />
        <CompDetail />
        <CompHourly />
        <CompDaily />
      </main>
      <CompMap />
    </AppContext.Provider>
  );
};
export default App;