import React from 'react';
import { useEffect } from 'react';
import { useContext } from 'react';
import { AppContext } from '../App';
import { fnGetAddress, fnGetWeather } from '../store/function';

const CompMap = () => {
  let {
    stLatLng, stChangeLatLng,
    stAddress, stChangeAddress,
    stWeather, stChangeWeather
  } = useContext(AppContext)

  useEffect(() => {
    //useEffect 안에서는 return false가 안됨....
    if (stLatLng) {
      let map = new window.google.maps.Map(document.getElementById('map'), {
        center: { lat: stLatLng.lat, lng: stLatLng.lng },
        zoom: 8
      })//map

      let center = { lat: stLatLng.lat, lng: stLatLng.lng }
      map.addListener('click', async (e) => {
        let latlng = {
          lat: e.latLng.lat(),
          lng: e.latLng.lng()
        }
        let addressArr = await fnGetAddress(latlng.lat, latlng.lng) //비동기로 보내서 async await로 받아야됨
        let address
        //주소가 없을때 예외처리를 해줘야 프로그램이 다운이 안됨
        try { //정상적인 상황일때
          address = addressArr[2].formatted_address //배열값 중 주소만 찍어주기
        } catch { //에러상황일때
          address = '주소를 가져올 수 없습니다.'
        }

        let yn = window.confirm(`"${address}"\n해당 위치의 날씨 정보로 변경하시겠습니까?`) //alert랑 비슷한 기능..사용자가 예/아니오를 선택할 수 있음
        if (yn) { //사용자가 yes를 눌렀을때..스테이트 변경
          stChangeLatLng(latlng) // stChangeLatLng({latlng.lat, latlng.lng})
          stChangeAddress(addressArr)

          //날씨정보를 받아오는데 시간이 걸려서 await 해줘야됨
          let weather = await fnGetWeather(latlng.lat, latlng.lng)
          stChangeWeather(weather)
          document.querySelector('.map-section').classList.remove('active')// 창끄기
          document.querySelector('.app-container').scrollTo({ top: 0 }) //스크롤을 top:0으로 옮김
        }
      })//click
    }
  })

  useEffect(() => {
    document.querySelector('.close-map').addEventListener('click',()=>{
      document.querySelector('.map-section').classList.remove('active')
    })
  },[])

  return (
    <section className='map-section'>
      <div id="map"></div>
      <button className='close-map'>
        <i className="fa-solid fa-xmark"></i>
      </button>
    </section>
  );
};

export default CompMap;