//필요한 함수들을 담아내는 파일
import { codeEnArr, codeKrArr } from "./conditionCode"

/*1. 현재위치 정보를 가져와서 위경도를 반환하는 비동기 함수---------------------------*/
export const fnGetCurrentLocation = () => { //해당 함수를 다른 파일에서 쓸꺼니깐 export써야됨
  return new Promise(resolve => {
    //현재위치 가져오기
    navigator.geolocation.getCurrentPosition((location) => {
      let latlang = { //리턴을 해줘야되서 두 개의 값을 객체로 싸서 한 개로 보냄
        lat: location.coords.latitude, //위도 
        lng: location.coords.longitude, //경도
      }
      // console.log(latlang.lat, latlang.lng) //37.4905399 126.7280586
      resolve(latlang) //끝났다고 알려주면서(resolve).....latlang 반환(latlang)

    })//getCurrentPosition
  }) //promise
} //fnGetCurrentLocation


/*2. 위경도를 받아서 주소를 가져와서 반환하는 함수---------------------------------------*/
export const fnGetAddress = (lat, lng) => {
  return new Promise(resolve => {

    let geocoder = new window.google.maps.Geocoder;
    let latLng = {
      lat: lat,
      lng: lng,
    };
    geocoder.geocode({ 'location': latLng }, function (address, status) { //address 실제주소...status 주소가 맞는지 확인하는 값
      if (status === "OK") {
        // console.log(address);
        resolve(address) //끝내면서 주소를 날려줌
      } else {
        // console.log("주소를 못 가지고 왔습니다");
        resolve(null) //null값 리턴..다른메세지를 출력하기 위해 값을 보냄
      }
    });

  })//Promise
} //fnGetAddress()

/*3. 위경도 정보를 받아서 날씨 정보를 반환하는 함수------------------- */
export const fnGetWeather = (lat, lng) => {
  return new Promise(resolve => {
    fetch(`
    https://api.openweathermap.org/data/3.0/onecall?lat=${lat}&lon=${lng}&appid=d6db100d697a383f6aa27a039d4618f0
    `).then((result) => { //fetch(`어디서 받아오는지..`).then(할일펑션..받아온 값을 파라메터로 부름)
      let data = result.json() //json형태로 받아오는 값을 js에서 쓸 수 있도록 변경해줌...비동기에서만 사용 가능(웹에서 가져오는건 비동기) // JSON.parse('로컬스토리지 가져온 데이터')..이건 동기에서 사용!!
      //console.log(data)
      resolve(data)
    }).catch(() => {
      alert('날씨 정보를 받아오지 못했습니다. 잠시 후 다시 시도해주세요')
      resolve(null)
    })
  })//promise

}//fnGetWeather()


/*4. 받아온 데이터를 출력할 데이터로 가공하는 함수 ---------------------------- */
export const fnOutput = (category) => {
  let time = new Date(category.dt * 1000)
  let year = time.getFullYear() //현재가 몇 년도인지
  let month = time.getMonth() + 1 //0월부터 출력돼서 1 더해줘야됨
  let date = time.getDate()
  let hour = time.getHours()
  let minute = time.getMinutes()
  minute = (minute<10)?'0'+minute:minute //10분보다 작으면 앞에 0 붙여줌
  
  let dayArr = ['SUN', 'MON', 'TUE', 'WED', 'THE', 'FRI', 'SAT']
  let day = dayArr[time.getDay()] //요일이 숫자로 출력돼서 배열로 만들어서 변경해줘야함

  let desc = codeKrArr[codeEnArr.indexOf(category.weather[0].description)] //현재 날씨 멘트가 영어 어레이에 몇번째 있니..숫자가 출력돼서..codeKoArr에서 같은 번호의 글자를 출력함
  let windDeg = category.wind_deg
  let windSpeed = category.wind_speed
  
  let rain = (category.rain) ? category.rain : 0 //현재날씨에 rain이 있으면..rain은 비올때만 생김..없으면 undifiend를 출력하기 때문에 0출력
  rain = (typeof(rain)==='object')?rain['1h']:rain //객체가 숫자형태로 들어가면 rain.1h이런식 말고 rain['1h'] 이렇게 찾아야됨
  let snow = (category.snow) ? category.snow : 0 
  snow = (typeof(snow)==='object')?snow['1h']:snow 

  let sunriseTime = (category.sunrise) && new Date(category.sunrise * 1000) //이항식..category.sunrise이 있으면 category.sunrise 값,,없으면 null
  let sunriseHour = (category.sunrise) && sunriseTime.getHours()
  let sunriseMin = (category.sunrise) && sunriseTime.getMinutes()
  let sunsetTime = (category.sunset) && new Date(category.sunset * 1000)
  let sunsetHour = (category.sunrise) && sunsetTime.getHours()
  let sunsetMin = (category.sunrise) && sunsetTime.getMinutes()

  let temp = (category.temp - 273.15).toFixed(1) //현재날씨(파라메터)의 temp //캘빈온도를 섭씨로 바꿔서 출력해야함
  let tempMin = (typeof (category.temp) === 'object') && (category.temp.min - 273.15).toFixed(1)// 최고온도 최소온도는 객체일때만 나옴...temp가 객체로 나왔을때&&categoty.temp.min-273.15
  let tempMax = (typeof (category.temp) === 'object') && (category.temp.max - 273.15).toFixed(1)
  let icon = category.weather[0].icon

  let feelsLike = (category.feels_like - 273.15).toFixed(1) //체감온도
  let clouds = category.clouds
  let uvi = category.uvi
  let humidity = category.humidity
  let pressure = category.pressure
  let visibility = (category.visibility/1000).toFixed(1)

  //let bg = category.weather[0].main
  return ({
    year, month, date, hour, minute, day,
    desc, windDeg, windSpeed, rain, snow, 
    sunriseHour, sunriseMin, sunsetHour, sunsetMin,
    temp, tempMin, tempMax,
    icon, feelsLike, clouds, uvi, humidity, pressure, visibility

  }) //여러 정보를 보내야하니깐 객체로 보냄
}//fnOutput