import React, { useContext } from 'react';
import { AppContext } from '../App';
import { fnOutput } from '../store/function';

const CompCurrent = () => {
  let { stAddress, stWeather } = useContext(AppContext) //구조분해로 필요한거만 뽑아씀
  //비동기 문법이라서 app이 생기기 전에 자식부터 만들어짐...그래서 자식에서 사용하고 싶은 변수가 생기지 않아서 바로 찾을 수 없음
  //그래서 부모가 만들어지기 전까지 실행하면 안됨
  if (!stAddress || !stWeather) return false //처음에 null값을 넣어놔서 부모가 생기기 전(스테이트 변수가 변하기 전)까지 실행 안됨
  let current = stWeather.current //현재 날씨 정보 객체
  let data = fnOutput(current) //출력하는 데이터는 함수에서 가공을 하여 전달 할 예정

  //오늘 날짜별 날씨 정보
  let today = stWeather.daily[0] //오늘날시 정보 객체 
  let todayData = fnOutput(today) //오늘 날짜별 날씨 정보를 가공해서 반환

  let nowMin = data.hour * 60 + parseInt(data.minute) //현재시간을 분으로 변경...
  let sunStart = parseInt(todayData.sunriseHour) * 60 + parseInt(todayData.sunriseMin) //일출 분 변환
  let sunEnd = parseInt(todayData.sunsetHour) * 60 + parseInt(todayData.sunsetMin) //일몰 분 변환
  let deg = (nowMin - sunStart) / (sunEnd - sunStart) * 180//(현재시간-일출)/(일몰-일출)*180
  if(deg<0){deg=0}
  if(deg>180){deg=180}

  /*주소가져오기*/
  let address
  try{
    address = stAddress[2].formatted_address
  }catch{
    address = '확인되지 않는 주소입니다.'
  }


  const fnOpenDetail = (e) => {
    e.currentTarget.classList.toggle('active')
    document.querySelector('.detail-section').classList.toggle('active')
  }


  return (
    <section className='current-section'>
      <div className='inner'>
        <p className='address'>
         {address}
        </p>
        <div className="current-info">
          <div className="wrap">
            <div className='sun-set-rise'> {/*일출, 일몰 시간*/}
              <div className="circle"></div>
              <time className='sunrise'>
                <i className="fa-solid fa-sun"></i>
                {todayData.sunriseHour}:{todayData.sunriseMin}
              </time>
              <time className='sunset'>
                <i className="fa-solid fa-moon"></i>
                {todayData.sunsetHour}:{todayData.sunsetMin}
              </time>
              <div className='sun' style={{ transform: `rotate(${deg}deg)` }}>
                <i className="fa-solid fa-sun"></i>
              </div>
            </div>

            <p className='temp-min-max'> {/*최저, 최고온도*/}
              <b>{todayData.tempMin}<sup>&#176;</sup></b>
              /
              <b>{todayData.tempMax}<sup>&#176;</sup></b>
            </p>

            <p className='temp'> {/*현재온도*/}
              <img src={`${process.env.PUBLIC_URL}/img/icons/${data.icon}.gif`} alt="" />
              <span>
                <b>{data.temp}</b>
                <sup>&#8451;</sup>
              </span>
            </p>{/*temp*/}

            <p className='desc'>{/*날씨멘트*/}
              " {data.desc} "
            </p>{/*desc*/}

            <p className='rain-snow'>{/*강수량 적설량*/}
              <b className='rain'>
                <i className="fa-solid fa-umbrella"></i>
                {data.rain}<small>mm</small>
              </b>{/*rain*/}
              <b className='snow'> 
              <i className="fa-regular fa-snowflake"></i>
              {data.snow}<small>mm</small>
              </b>
            </p>{/*rain-snow*/}

            <p className='wind'>{/*풍향*/}
              <b className='wind-deg'>
                <i className="fa-solid fa-location-arrow" style={{ transform: `rotate(${data.windDeg - 45}deg)` }}> </i> 
              </b>

              <b className='wind-speed'>
                <i className="fa-solid fa-wind"></i>
                {data.windSpeed}<small>m/s</small>
              </b>
            </p>{/*wind*/}

          </div> {/*wrap*/}
        </div> {/*current-info*/}


        <p className='time'>{/*시간 과 일자*/}
          <time className='hh-mm'>
            <b>
              {(data.hour <= 12) ? data.hour : data.hour - 12}: {/*13시..이런식으로 나와서 오전오후 계산해서 출력해얗삼 */}
              {data.minute}
            </b>
            <small>{(data.hour < 12) ? 'AM' : 'PM'}</small>
          </time>

          <time className='yy-dd'>
            {data.year}년 {data.month}월 {data.date}일
            <small>({data.day})</small>
          </time>
        </p>{/*time*/}

      </div>{/*inner*/}
      
      <button className='more' onClick={fnOpenDetail}>
        <i className="fa-solid fa-circle-plus plus"></i>
        <i className="fa-solid fa-circle-minus minus"></i>
      </button>
    </section>
  );
};

export default CompCurrent;